  export async function userLogin(username, password) {
    let body;
    let resData;
    const options = {
      headers: { 
        "Content-type": "application/json",
          "Content-Length": "<calculated when request is sent>",
           "Host": "<calculated when request is sent>",
           "Accept": "*/*",
       },
    };
    if (["@"].some((a) => a !== username)) {
      body = {
        mobileNumber: username,
        password: password,
      };
    } else {
      body = {
        email: username,
        password: password,
      };
    }
    const data=body
      axios
      .post(normalLogin + "/api/auth-service/auth/sign-in", data,options)
      .then(function (response) {
        console.log("res" + response.data);
        resData = response.data;
      })
      .catch(function (error) {
        console.log(error);
      });
  
    return resData;
  }    
  export async function userSigup(name,username, password) {
    let body;
    let resData;
    const options = {
      headers: {
         "Content-type": "application/json",
          "Content-Length": "<calculated when request is sent>",
           "Host": "<calculated when request is sent>",
           "Accept": "*/*",
     },
    };

    body={  
      alternativeMobile: "",
      email: name,
      gender: "male",
      lastName: "",
      middleName: "",
      firstName: username,
      mobileNumber: "",
      password: password,
      profileImage: "",
      proofNumber: "",
      proofPath: "",
      roleId: "3fa85f64-5717-4562-b3fc-2c963f66afa6"
    }
    
    const data = body
    await axios
      .post(normalLogin + "/auth-service/auth/sign-in", data,options)
      .then(function (response) {
        console.log("res" + response.data);
        resData = response.data;
      })
      .catch(function (error) {
        console.log(error);
        resData=error;
      });
  
    return resData;
  }